.modal-base {
    width: 100%;
    height: 100%;
    background-color: grey;
    position: fixed;
    z-index: 5;
    opacity: 1;
    -webkit-transition: opacity 1s linear 0.5s;
    -o-transition: opacity 1s linear 0.5s;
    transition: opacity 1s linear 0.5s;
    top: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.displayNone {
    display: none;
    opacity: 0;
}

.modal-body {
    height: 500px;
    background-color: white;
    padding: 30px 10px;
    border-radius: 5px;
    opacity: 1;
}

.scrollable {
    overflow: scroll;
}

.max-height-500 {
    max-height: 500px;
}




