.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.text-right {
    text-align: right;
}

.text-left {
    text-align: left;
}
.text-center {
    text-align: center;
}

button {
    height: 30px;
    font-size: 15px;
}

.main-container {
    /*margin: 40px;*/
    /*margin-top: 80px;*/
}

.position-sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 150px;
}


.selectViewBar > *  {
    border-right: 1px solid #aaa;
    padding: 5px;
    color: #000;
    text-decoration: none;
}

.selectViewBar > *:last-child {
    border-right: none;
}

.selectViewBar.white > * {
    color: #ffffff;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



